<template>
  <div>
    <!-- <h3 class="page-head">
      <router-link to="/admin/setting">
        <i class="el-icon-arrow-left"></i>
      </router-link>/
    </h3> -->
    <div class="title"></div>
    <div class="content">
      <el-form ref="form" :model="formData" :rules="rules" label-width="120px">
        <el-form-item label="APPID" prop="app_id">
          <el-input v-model="formData.app_id" placeholder="APPID ID"></el-input>
        </el-form-item>
        <el-form-item label="SecretID" prop="secret_id">
          <el-input v-model="formData.secret_id" placeholder="SecretID "></el-input>
        </el-form-item>
        <el-form-item label="SecretKEY" prop="secret_key">
          <el-input v-model="formData.secret_key" placeholder="SecretKEY "></el-input>
        </el-form-item>
        <el-form-item label="Bucket" prop="bucket">
          <el-input v-model="formData.bucket" placeholder="bucket"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <!-- <el-input v-model="formData.region"></el-input> -->
          <el-select v-model="formData.region" placeholder="">
            <el-option v-for="(region, key) in regionList" :value="key" :key="key" :label="region"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Url" prop="url">
          <el-input v-model="formData.url" placeholder="url /http://abc.com"></el-input>
          <div class=""><span v-if="formData.app_id && formData.region && formData.bucket">{{formData.bucket + '-' + formData.app_id + '.cos.'+ formData.region +'.myqcloud.com'}}</span>
          </div>
        </el-form-item>
         <el-form-item label="" prop="path">
          <el-input v-model="formData.path" placeholder="COS"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        key: 'cloud_cosv5',
        app_id: '',
        secret_id: '',
        secret_key: '',
        bucket: '',
        region: '',
        url: '',
        path: ''
      },
      regionList: {
        'ap-beijing-1': '',
        'ap-beijing': '',
        'ap-shanghai': '',
        'ap-guangzhou': ')',
        'ap-chengdu': '',
        'ap-chongqing': '',
        'ap-shenzhen-fsi': '',
        'ap-shanghai-fsi': '',
        'ap-beijing-fsi': '',
        'ap-hongkong': '',
        'ap-singapore': '',
        'ap-mumbai': '',
        'ap-seoul': '',
        'ap-bangkok': '',
        'ap-tokyo': '',
        'na-siliconvalley': '',
        'na-ashburn': '',
        'na-toronto': '',
        'eu-frankfurt': '',
        'eu-moscow': '',
      },
      rules: {
        app_id: [{ required: true, message: 'APPID', trigger: 'blur' }],
        secret_id: [{ required: true, message: 'SecretID', trigger: 'blur' }],
        secret_key: [{ required: true, message: 'SecretKEY', trigger: 'blur' }],
        bucket: [{ required: true, message: 'Bucket', trigger: 'blur' }],
        region: [{ required: true, message: 'Bucket', trigger: 'blur' }],
        // cdn: [{ required: true, message: 'Url', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.init()
  },
  methods: {
    init() {
      this.$post('/admin/setting/cos', {})
        .then(res => {
          this.formData = Object.assign(this.formData, res.setting);
          this.formData.key = res.key
        })
        .catch(() => {})
    },
    onSubmit() {
      this.$refs['form'].validate(valid => {
        if (valid) {
          this.$post('/admin/setting/save', {'key': this.formData['key'], 'setting' : this.formData})
            .then(() => {
              this.$message('')
            })
        } else {
          return false
        }
      })
    }
  }
}
</script>

