<template>
  <div class="we7-document-history">
    <h3 class="page-head">
      
    </h3>
    <div class="search-box">
      <div class="demo-input-suffix">
        <el-input v-model="keyword" placeholder="" clearable @keyup.enter.native="getList">
          <i slot="suffix" class="el-input__icon el-icon-search" @click="getList"></i>
        </el-input>
      </div>
      <el-select v-model="time" placeholder="" @change="getList">
        <el-option label="" value="0"></el-option>
        <el-option label="" value="7"></el-option>
        <el-option label="" value="30"></el-option>
        <el-option label="" value="90"></el-option>
    </el-select>
    </div>
    <el-table class="w7-table" :data="docList" ref="multipleTable"
      :header-cell-style="{background:'#f7f9fc',color:'#606266'}"
      empty-text="">
      <el-table-column label="">
        <div class="doc-icons" slot-scope="scope">
          <i class="wi wi-document color-blue"></i>
          <span class="name" @click="readDoc(scope.row.id)">{{ scope.row.name }}</span>
          <i class="wi wi-star color-yellow" v-if="scope.row.has_star"></i>
          <div class="we7-label" v-if="!scope.row.is_public">
            <i class="wi wi-lock" ><span class="font"></span></i>
          </div>
        </div>
      </el-table-column>
      <el-table-column label="" prop="author.name"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          {{ timestampFormat(scope.row.time) }}
        </template>
      </el-table-column>
      <el-table-column label="" align="right">
        <div class="oper" slot-scope="scope">
          <el-tooltip effect="dark" content="" placement="bottom">
            <i class="wi wi-delete" @click="del(scope.row.id)"></i>
          </el-tooltip>
        </div>
      </el-table-column>
    </el-table>
    <div class="btns">
      <el-pagination
        background
        @current-change = "getList"
        layout="prev, pager, next, total"
        prev-text=""
        next-text=""
        :current-page.sync = "currentPage"
        :page-count="pageCount"
        :total="total"
        :hide-on-single-page = "true"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      keyword: '',
      time: '',
      loading: false,
      docList: [],//
      currentPage: 1,//
      pageCount: 0,//
      total: 0//
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.loading = true
      this.$post('/admin/document/operate-log',{
        page: this.currentPage,
        name: this.keyword,
        time: this.time
      })
        .then(res => {
          this.docList = res.data
          this.pageCount = res.page_count
          this.total = res.total
          this.loading = false
        })
    },
    timestampFormat( timeString ) {
      var timestamp = Date.parse(timeString)/1000
      function zeroize( num ) {
          return (String(num).length == 1 ? '0' : '') + num;
      }

      var curTimestamp = parseInt(new Date().getTime() / 1000); //
      var timestampDiff = curTimestamp - timestamp; // 

      var curDate = new Date( curTimestamp * 1000 ); // 
      var tmDate = new Date( timestamp * 1000 );  // 

      var Y = tmDate.getFullYear(), m = tmDate.getMonth() + 1, d = tmDate.getDate();
      var H = tmDate.getHours(), i = tmDate.getMinutes();

      if ( timestampDiff < 60 ) { // 
        return "";
      } else if( timestampDiff < 3600 ) { // 
        return Math.floor( timestampDiff / 60 ) + "";
      } else if ( curDate.getFullYear() == Y && curDate.getMonth()+1 == m && curDate.getDate() == d ) {
        return '' + zeroize(H) + ':' + zeroize(i);
      } else {
        var oneDayDate = new Date( (curTimestamp - 86400) * 1000 ); // 
        if ( oneDayDate.getFullYear() == Y && oneDayDate.getMonth()+1 == m && oneDayDate.getDate() == d ) {
          return '' + zeroize(H) + ':' + zeroize(i);
        } else {
          return  Y + '' + zeroize(m) + '' + zeroize(d) + ' ' + zeroize(H) + ':' + zeroize(i);
        }
      }
    },
    del(id) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$post('/admin/document/delete-operate-log',{
          document_id: id
        })
          .then(() => {
            this.getList()
            this.$message('')
          })
      })
    },
    readDoc(id) {
      let routeUrl = this.$router.resolve({
        path: "/chapter/" + id
      })
      window.open(routeUrl.href, '_blank')
    }
  }
}
</script>

<style lang="scss">
.we7-document-history {
  .search-box {
    justify-content: flex-start;
    .demo-input-suffix {
      margin-right: 20px;
    }
  }
}
</style>