<template>
  <div class="permission">
    <div class="select-power search-box">
        <span class="name"></span>
        <el-select v-model="is_public" placeholder="" @change="search">
            <el-option label="" value="0"></el-option>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
        </el-select>
        <div class="demo-input-suffix">
          <el-input v-model="keyword" placeholder="" clearable @keyup.enter.native="search">
            <i slot="suffix" class="el-input__icon el-icon-search" @click="search"></i>
          </el-input>
        </div>
        <div class="more-edit" @click="dialogEditInfoVisible = true"></div>
    </div>
    <el-table class="w7-table" :data="docList" empty-text="" row-key="id" ref="multipleTable" :header-cell-style="{background:'#f7f9fc',color:'#606266'}">
        <el-table-column reserve-selection type="selection" width="55"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
              <i class="wi wi-folder"></i>
              <span style="margin-left: 10px">{{ scope.row.name }}</span>
              <div v-if="!scope.row.is_public" style="display:inline-block;padding:0 5px;margin-left: 20px;background:#fff1de;color:#ff8600;">
              <i class="el-icon-lock" ><span style="margin-left: 5px;"></span></i>
              </div>
          </template>
        </el-table-column>
        <el-table-column label="" align="right">
        <template slot-scope="scope">
            <el-radio-group v-model="scope.row.cur_role" class="ownership">
              <template v-for="item in scope.row.role_list">
                <el-tooltip effect="dark" content="" placement="bottom"
                  v-if="item.id == 1"
                  :key="item.id">
                    <el-radio :label="1">{{item.name}}</el-radio>
                </el-tooltip>
                <el-tooltip effect="dark" content="" placement="bottom"
                  v-if="item.id == 2"
                  :key="item.id">
                    <el-radio :label="2">{{item.name}}</el-radio>
                </el-tooltip>
                <el-tooltip effect="dark" content="" placement="bottom"
                  v-if="item.id == 3"
                  :key="item.id">
                    <el-radio :label="3">{{item.name}}</el-radio>
                </el-tooltip>
              </template>
            </el-radio-group>
        </template>
        </el-table-column>
        <div class="nodata" slot="empty">
          <p></p>
        </div>
    </el-table>
    <div class="get-more" v-if="currentPage != pageCount && pageCount > 1">
      <el-button type="text" @click="getMore"></el-button>
    </div>
    <div class="btns">
        <el-button type="primary" @click="save"></el-button>
    </div>
    <!--  -->
    <el-dialog class="w7-dialog" title="" :visible.sync="dialogEditInfoVisible" :close-on-click-modal="false" center>
        <el-form label-width="120" style="margin-left:50px;">
        <el-form-item label="">
            <el-radio-group v-model="radio" class="ownership">
            <!-- <el-radio :label="1"></el-radio> -->
            <el-radio :label="2"></el-radio>
            </el-radio-group>
        </el-form-item>
        <el-form-item label="">
            <el-radio-group v-model="radio1" class="ownership">
            <!-- <el-radio :label="1"></el-radio> -->
            <el-radio :label="2"></el-radio>
            <el-radio :label="3"></el-radio>
            </el-radio-group>
        </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editAll"> </el-button>
        <el-button @click="dialogEditInfoVisible = false"> </el-button>
        </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ['user_id'],
  data() {
    return {
      is_public: '',
      keyword: '',
      docList: [],//
      currentPage: 1,//
      pageCount: 1,//
      total: 0,//
      dialogEditInfoVisible: false,
      radio: 2,
      radio1: 2,
      selectRows: []//
    }
  },
  created() {
    this.getList()
  },
  methods: {
    search() {
      this.currentPage = 1
      this.getList()
    },
    getMore() {
      this.currentPage++
      this.getList('more')
    },
    getList(val) {
      this.$post('/admin/document/all-by-uid',{
        user_id: this.user_id,
        page: this.currentPage,
        name: this.keyword,
        is_public: this.is_public
      })
        .then(res => {
          if (val == 'more') {
            this.docList = [...this.docList,...res.data]
          } else {
            this.docList = res.data
          }
          this.pageCount = res.page_count
          this.total = res.total
        })
    },
    save() {
      let document_permission = []
      for (const i in this.docList) {
        if(this.docList[i].cur_role) {
          document_permission.push({
            document_id: this.docList[i].id,
            permission: this.docList[i].cur_role
          })
        }
      }
      if (document_permission.length) {
        this.$post('/admin/user/batch-update-permission',{
          user_id: this.user_id,
          document_permission: document_permission
        })
          .then(() => {
            this.$message('')
            this.$router.push('/admin/user')
          })
      } else {
        this.$router.push('/admin/user')
      }
    },
    editAll() {
      let rows = this.$refs.multipleTable.selection
      for (const i in rows) {
        if (rows[i].is_public) {
          rows[i].cur_role = Number(this.radio)
        } else {
          rows[i].cur_role = Number(this.radio1)
        }
      }
      this.dialogEditInfoVisible = false
    }
  }
}
</script>

