<template>
  <div class="container">
    <h3 class="page-head">
      
    </h3>
    <div class="search-box">
      <div class="demo-input-suffix">
        <el-input v-model="keyword" placeholder="" clearable @keyup.enter.native="search">
        <i slot="suffix" class="el-input__icon el-icon-search" @click="search"></i>
        </el-input>
      </div>
      <div class="demo-input-btn">
        <router-link to="user/create" type="button" class="el-button el-button--primary"></router-link>
      </div>
    </div>
    <el-table class="w7-table" :data="userList" ref="multipleTable"
      :header-cell-style="{background:'#f7f9fc',color:'#606266'}"
      empty-text="">
      <el-table-column label="" prop="username"></el-table-column>
      <el-table-column label="" prop="created_at" sortable column-key="date">
        <template slot-scope="scope">
          <div v-if="UserInfo.id != scope.row.id">{{scope.row.created_at}}</div>
        </template>
      </el-table-column>
      <el-table-column label="" prop="role"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.manage_doc_count}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.operate_doc_count}}</span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <span>{{scope.row.read_doc_count}}</span>
        </template>
      </el-table-column>
      <el-table-column label="" align="right">
        <div class="oper" slot-scope="scope">
          <el-tooltip effect="dark" content="" placement="bottom">
            <i class="wi wi-tools" @click="openEditorUser(scope.row)"></i>
          </el-tooltip>
          <el-tooltip effect="dark" content="" placement="bottom">
            <i class="wi wi-quanxian2" @click="openPermission(scope.row.id)"></i>
          </el-tooltip>
          <el-tooltip effect="dark" content="" placement="bottom">
            <i class="wi wi-delete" @click="deleteSelectRows(scope.row.id)" v-if="UserInfo.id != scope.row.id"></i>
          </el-tooltip>
        </div>
      </el-table-column>
    </el-table>
    <div class="btns">
      <el-pagination
        background
        @current-change = "getuserlist"
        layout="prev, pager, next, total"
        prev-text=""
        next-text=""
        :current-page.sync = "currentPage"
        :page-count="pageCount"
        :total="total"
        :hide-on-single-page = "true"
      >
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog class="w7-dialog" title="" :visible.sync="dialogEditUserVisible" :close-on-click-modal="false" center>
      <el-form :model="formData" label-width="120px">
        <el-form-item label="">
          <el-input v-model="formData.username"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.userpass" type="password"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formData.confirm_userpass" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="editorUser"> </el-button>
        <el-button @click="dialogEditUserVisible = false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      keyword: '',
      userList: [],//
      currentPage: 1,//
      pageCount: 0,//
      total: 0,//
      // showPermission: false,//
      // userId: '',//id
      dialogEditUserVisible: false,//
      formData: {
        id: '',
        username: '',
        userpass: '',
        confirm_userpass: ''
      }
    }
  },
  computed: {
    ...mapGetters({ UserInfo: 'UserInfo' })
  },
  created() {
    this.$store.dispatch('getUserInfo')
    this.getuserlist()
  },
  methods: {
    search() {
      this.currentPage = 1
      this.getuserlist()
    },
    getuserlist() {
      this.$post('/admin/user/search', {
        page: this.currentPage,
        username: this.keyword
      })
        .then(res => {
           this.userList = res.data
           this.currentPage = res.page_current
           this.pageCount = res.pageCount
           this.total = res.total
        })
    },
    openEditorUser(row) {
      this.formData.id = row.id
      this.formData.username = row.username
      this.dialogEditUserVisible = true
    },
    editorUser() {
      this.$post('/admin/user/update', this.formData)
        .then(() => {
          this.$message('')
          this.getuserlist()
          this.dialogEditUserVisible = false
        })
    },
    openPermission(id) {
      this.$router.push('/admin/user/' + id)
    },
    deleteSelectRows(id) {
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$post('/admin/user/delete-by-ids',{
            ids: id
          })
            .then(res => {
              this.$message(res)
              this.getuserlist()
              this.selectRowID = ''
            })
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.container{
  padding:30px 40px 0 40px;
}
.el-input__icon{
  color:#3296fa;
}
.btns {
  margin-top: 20px;
  button {
    margin: 0 20px;
    padding: 12px 30px;
  }
  .el-pagination {
    margin-top:0;
  }
}
</style>