<template>
  <el-container class="w7-document-chapter">
    <el-aside class="w7-aside-chapter" width="202px">
      <div class="w7-aside-chapter-head">
        <p>{{ docName }}</p>
      </div>
      <el-input placeholder="" v-model="filterText" clearable>
        <i slot="suffix" class="el-input__icon el-icon-search"></i>
      </el-input>
      <div class="icon-box">
        <el-tooltip effect="dark" content="" placement="bottom">
          <i class="wi wi-document" @click="clickIconAddNode(false)"></i>
        </el-tooltip>
        <el-tooltip effect="dark" content="" placement="bottom">
          <i class="wi wi-folder" @click="clickIconAddNode(true)"></i>
        </el-tooltip>
        <el-tooltip effect="dark" content="" placement="bottom">
          <i class="wi wi-view" @click="readDoc"></i>
        </el-tooltip>
        <el-tooltip effect="dark" content="" placement="bottom" v-if="has_manage">
          <i class="wi wi-tools" @click="settingDoc"></i>
        </el-tooltip>
      </div>
      <el-tree class="w7-tree" :data="chapters" :props="defaultProps" empty-text=""
        ref="chaptersTree"
        node-key="id"
        :expand-on-click-node="true"
        :highlight-current="true"
        :default-expanded-keys="defaultExpanded"
        :filter-node-method="filterNode"
        @node-contextmenu="rightClick"
        @node-click="handleNodeClick"
        draggable
        @node-drop="handleDrop"
        :allow-drop="allowDrop">
        <span class="custom-tree-node" slot-scope="{ node, data }">
          <span>
            <i class="wi wi-folder" v-if="data.is_dir == 1"></i>
            <i class="wi wi-document" v-if="data.is_dir == 0"></i>
            <span style="margin-left:10px;">{{ node.label }}</span>
          </span>
          <span class="point3" @mousemove='updateXY' @click.stop="rightClick(false, data, node)"><span>...</span></span>
        </span>
      </el-tree>
      <div id="menu-bar" class="menu-bar" v-show="menuBarVisible">
        <ul class="menu">
          <template v-if="rightSelectNodeObj.is_dir == 1">
            <li class="menu__item" @click="addChildNode(true)"></li>
            <li class="menu__item" @click="addChildNode(false)"></li>
            <li class="menu__item" @click="updateNode(true)"></li>
            <li class="menu__item" @click="openMoveDialog(true)"></li>
            <li class="menu__item" @click="removeNode"></li>
          </template>
          <template v-if="rightSelectNodeObj.is_dir == 0">
            <li class="menu__item" @click="defaultFile"></li>
            <li class="menu__item" @click="updateNode(false)"></li>
            <li class="menu__item" @click="openMoveDialog(false)"></li>
            <!-- <li class="menu__item" @click=""></li> -->
            <li class="menu__item" @click="removeNode"></li>
          </template>
        </ul>
      </div>
    </el-aside>
    <el-main>
      <editors :chapterId="selectNodeObj.id" :chapterName="selectNodeObj.name" v-if="selectNodeObj && selectNodeObj.id && selectNodeObj.is_dir == 0"></editors>
    </el-main>
    <!--  -->
    <el-dialog class="w7-dialog only-input-dialog" :title="dialogTitle" :visible.sync="dialogVisible" :close-on-click-modal="false">
      <el-form :model="addNodeObj" label-width="100px" @submit.native.prevent>
        <el-form-item :label="dialogFormLabel">
          <el-input v-model="addNodeObj.name" @keyup.enter.native="confirmBtn"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmBtn"> </el-button>
        <el-button @click="dialogVisible = false"> </el-button>
      </div>
    </el-dialog>
    <!-- tree -->
    <el-dialog class="w7-dialog" :title="dialogMoveTitle" :visible.sync="dialogMoveVisible" :close-on-click-modal="false">
      <el-form label-width="100px">
        <el-form-item label="">
          <el-select v-model="moveDoc" placeholder=""
            filterable
            remote
            reserve-keyword
            :remote-method="remoteMethod"
            @change="changeDoc">
            <el-option
              v-for="item in docList"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-cascader
            v-model="moveClass"
            :options="docChapters"
            :props="{ value: 'id', label: 'name'}"
            :clearable="true"
            :change-on-select="true"></el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="moveNode"> </el-button>
        <el-button @click="dialogMoveVisible = false"> </el-button>
      </div>
    </el-dialog>
  </el-container>
</template>

<script>
import editors from './editors.vue'
export default {
  name: 'chapter',
  components: {
    editors
  },
  data() {
    return {
      docName: '',
      has_manage: true,
      filterText: '',
      chapters: [],//
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      defaultExpanded: [],//
      menuBarVisible: false,
      clientX: '',
      clientY: '',
      selectNodeObj: {},//data
      rightSelectNodeObj: {},//Object
      rightSelectNode: {},//Node
      addFirst: true,//
      addNodeObj: {
        name: '',
        is_dir: 1
      },//
      dialogTitle: '',
      dialogFormLabel: '',
      dialogVisible: false,//
      dialogMoveVisible: false,//
      dialogMoveTitle: '',
      moveDoc: '',
      moveClass: '',
      docList: [],
      docChapters: []
    }
  },
  watch: {
    filterText(val) {
      this.$refs.chaptersTree.filter(val);
    },
    dialogVisible(val) {
      if (val) {
        setTimeout(() => {
          document.querySelector(".only-input-dialog .el-input__inner").focus()
        }, 300);
      }
    }
  },
  created() {
    this.getChapters()
  },
  methods: {
    getChapters() {
      this.$post('/admin/chapter/detail', {
        document_id: this.$route.params.id
      })
        .then(res => {
          this.docName = res.document.name
          this.has_manage = res.acl.has_manage
          this.chapters = res.catalog
        })
    },
    readDoc() {
      let routeUrl = this.$router.resolve({
        path: "/chapter/" + this.$route.params.id
      })
      window.open(routeUrl.href, '_blank')
    },
    settingDoc() {
      this.$router.push('/admin/document/' + this.$route.params.id)
    },
    filterNode(value, data) {
      if (!value) return true;
      return data.name.indexOf(value) !== -1;
    },
    handleNodeClick(obj) {
      if(this.menuBarVisible) {this.menuBarVisible = false}
      this.selectNodeObj = obj
    },
    updateXY(event) {
      this.clientX = event.clientX
      this.clientY = event.clientY
    },
    rightClick(MouseEvent, object, Node) {
      // console.log('---event:', MouseEvent)
      // console.log('--- data :', object)
      // console.log('--- Node:', Node)
      // console.log('---:', element)
      this.menuBarVisible = false //  n true
      this.menuBarVisible = true  // 
      const menuBar = document.querySelector('#menu-bar')
      if(!MouseEvent) {
        menuBar.style.left = this.clientX + 'px'
        menuBar.style.top = this.clientY + 'px'
      }
      menuBar.style.left = MouseEvent.clientX + 'px'
      menuBar.style.top = MouseEvent.clientY + 'px'
      this.rightSelectNodeObj = object
      this.rightSelectNode = Node
      // documentremoveRightClickEvent
      document.addEventListener('click', this.removeRightClickEvent)
    },
    removeRightClickEvent() {
      this.menuBarVisible = false
      document.removeEventListener('click', this.removeRightClickEvent)
    },
    clickIconAddNode(bool) {
      this.addFirst = true
      this.dialogTitle = bool ? '' : ''
      this.dialogFormLabel = bool ? '' : ''
      this.addNodeObj.name = ''
      this.addNodeObj.is_dir = bool ? 1 : 0
      this.dialogVisible = true
      this.rightSelectNode = {}
      this.rightSelectNodeObj = {}
    },
    updateNode(bool) {
      this.dialogTitle = ''
      this.addNodeObj.name = this.rightSelectNodeObj.name
      this.dialogFormLabel = bool ? '' : ''
      this.dialogVisible = true
    },
    addChildNode(bool) {
      this.addFirst = false
      if (this.rightSelectNode.level == 2 && bool) {
        this.$message('')
        return
      }
      this.dialogTitle = bool ? '' : ''
      this.dialogFormLabel = bool ? '' : ''
      this.addNodeObj.name = ''
      this.addNodeObj.is_dir = bool ? 1 : 0
      this.dialogVisible = true
    },
    confirmBtn() {
      if(!this.addNodeObj.name) {
        this.$message('')
        return
      }
      if(this.dialogTitle == '' || this.dialogTitle == '') {
        this.$post('/admin/chapter/create', {
          document_id: this.$route.params.id,
          parent_id: this.addFirst ? 0 : this.rightSelectNode.data.id,
          is_dir: this.addNodeObj.is_dir,
          name: this.addNodeObj.name
        })
          .then(res => {
            let newChild = res
            if(!this.addFirst) {
              let data = this.rightSelectNodeObj
              if (!data.children) {
                this.$set(data, 'children', []);
              }
              data.children.push(newChild)
            }else {
              this.chapters.push(newChild)
            }
            this.$message('')
            this.dialogVisible = false
            this.$nextTick(() => {
              //
              this.$refs.chaptersTree.setCurrentKey( newChild.id )
              this.handleNodeClick(this.$refs.chaptersTree.getCurrentNode())
              //
              this.defaultExpanded.push(newChild.id)
            })
          })
          .catch(() => {
            this.dialogVisible = false
          })
      }
      if(this.dialogTitle == '') {
        this.$post('/admin/chapter/update', {
          document_id: this.$route.params.id,
          chapter_id: this.rightSelectNodeObj.id,
          name: this.addNodeObj.name
        })
          .then(() => {
            this.$message('')
            this.rightSelectNodeObj.name = this.addNodeObj.name
            this.dialogVisible = false
          })
          .catch(() => {
            this.dialogVisible = false
          })
      }
    },
    removeNode() {
      var arrId =[]
      arrId.push(this.rightSelectNodeObj.id)
      //,
      if (this.rightSelectNodeObj.is_dir && this.rightSelectNodeObj.children && this.rightSelectNodeObj.children.length) {
        let getArrId = function(array) {
          array.forEach(item => {
            arrId.push(item.id)
            if (item.children && item.children.length) {
              getArrId(item.children)
            }
          })
        }
        getArrId(this.rightSelectNodeObj.children)
      }
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //this.rightSelectNode
        this.$post('/admin/chapter/delete', {
          document_id: this.$route.params.id,
          chapter_id: arrId
        })
          .then(() => {
            let node = this.rightSelectNode
            let data = this.rightSelectNodeObj
            let parent = node.parent;
            let children = parent.data.children || parent.data
            let index = children.findIndex(d => d.id === data.id)
            children.splice(index, 1)
            this.$message('')
            this.selectNodeObj = {}
          })
      }).catch(() => {
      })
    },
    handleDrop(draggingNode, dropNode, dropType) {
      this.$post('/admin/chapter/sort', {
        document_id: this.$route.params.id,
        chapter_id: draggingNode.data.id,
        target: {
          chapter_id: dropNode.data.id,
          parent_id: dropNode.data.parent_id,
          position: dropType
        }
      })
        .then(() => {
          this.$message('')
        })
    },
    allowDrop(draggingNode, dropNode, type) {
      if (dropNode.data.is_dir || type !== 'inner') {
        return true
      }
    },
    openMoveDialog(bool) {
      this.dialogMoveTitle =  bool ? '' : ''
      this.moveDoc = ''
      this.moveClass = ''
      this.dialogMoveVisible = true
    },
    remoteMethod(query) {
      if (query !== '') {
        this.$post('/admin/document/all',{
          keyword: query
        })
          .then(res => {
            this.docList = res.data
          })
      } else {
        this.options = [];
      }
    },
    changeDoc(id) {
      this.$post('/admin/chapter/detail', {
        document_id: id

      })
        .then(res => {
          //children
          this.docChapters =  this.deleteA(this.deleteA(res.catalog))
        })
    },
    deleteA(arr) {
      if(arr.length) {
        for(let i = arr.length-1;i>= 0; i--) {
          if(!arr[i]['is_dir']) {
            arr.splice(i, 1)
          } else if(arr[i].children && !arr[i].children.length) {
            delete arr[i].children
          } else if(arr[i].children){
            this.deleteA(arr[i]['children'])
          }
        }
      }
      return arr
    },
    moveNode() {
      if(!this.moveDoc) {
        this.$message('')
        return
      }
      let id = 0
      if (this.moveClass.length == 1) {
        id = this.moveClass[0]
      } else if (this.moveClass.length == 2) {
        id = this.moveClass[1]
      }
      this.$post('/admin/chapter/sort', {
        document_id: this.$route.params.id,
        chapter_id: this.rightSelectNodeObj.id,
        target: {
          document_id: this.moveDoc,
          chapter_id: id,
          position: 'move'
        }
      })
        .then(() => {
          this.$message('')
          this.dialogMoveVisible = false
          this.getChapters()
        })
    },
    defaultFile() {
      this.$post('/admin/chapter/default-show', {
        document_id: this.$route.params.id,
        chapter_id: this.rightSelectNodeObj.parent_id,
        show_chapter_id: this.rightSelectNodeObj.id
      })
        .then(() => {
          this.$message('')
        })
    }
  }
}
</script>

