      p {
        font-size: 16px;
        color: #4d4d4d;
        text-align: center;
      }
    }
    .el-input {
      width: 180px;
      margin: 25px 10px;
      /deep/ input {
        height: 34px;
        border: solid 1px #eeeeee;
        border-radius: 0;
      }
      /deep/ i {
        line-height: 34px;
      }
    }
  }
  .icon-box{
    width:100%;
    display: flex;
    justify-content: space-around;
    margin-bottom: 35px;
    font-size:14px;
    color:#b6b5b5;
    .el-tooltip {
      cursor: pointer;
    }
  }
}
.w7-tree {
  .el-tree-node__content {
    &:hover .custom-tree-node .point3 {
      display: inline-block;
