    .custom-tree-node {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .point3 {
        display: none;
        color: #409eff;
        span {
          display: block;
          font-size: 30px;
          margin-top: -23px;
          margin-right: 10px;
        }
      }
    }
  }
  .wi-document {
    color: #3296fa;
